.bar-chart{
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    width: calc(100% - 5px);
    /* width:900px; */
    height: calc(100% - 40px);
}

.legend-container{
    display: flex; 
    justify-content: center;
    margin-top: 20px;
}

.legend-items{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 20px;
}

.legend-label{
    white-space: nowrap;
}

.bar-chart-small-sx{
    display:none;
}

@media (max-width: 1024px) {
    .bar-chart{
        display: none;
    }

    .bar-chart-small-sx{
        /* display:block; */
        width:1000px;
        display:flex;
        flex-direction: column;
        align-items:center;
        justify-content: center;
        height: calc(100% - 40px);
    }
}